@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";



.slide-content {
	/*background-attachment: fixed;*/
	background-position: center;
	background-repeat: no-repeat;
	height: 100vh;
	max-height: 950px;
	position: relative;
}
/* click to continue footnote */
.slide-content footer {
	bottom: 5vh;
	position: absolute;
	width: 100%;
	z-index: 250;
}

.slide-content footer p {
	padding: 0 3px;
	text-align: right;
	text-shadow: 1px 0px 1px #343434;
}

.slide-content footer .arrow-button {
	display: inline;
	float: right;
    height: 60px;
    margin-right: 5%;
    opacity: 0.90;
    transform: translateY(-16px);
    width: 35px;
}


.slick-slide {
	height: 100vh;
	position: relative;
	background-size: cover;
	width: 100%;
}

.slick-slide .video-background {
	background-color: #343434;
}

.slick-slide .title-block {
	bottom: 20vh;
	font-size: 1.1em;
	left: 5%;
	position: absolute;
	right: 5%;
}

#slide-credits .title-block {
	font-size: 1.1em;
}

#slide-credits .title-block {
	bottom: 0vh;
}

.slick-slide .copy-block {
	bottom: 9vh;
	left: 2%;
	position: absolute;
	width: 95%;
	z-index: 100;
}

.slick-slide h3 {
	color: #FFF;
	font-size: 4rem;
	margin-bottom: 10px;
	text-shadow: 1px 1px 1px #343434;
}
 
.slick-slide p {
	color: #FFF;
	display: inline-block;
	font-size: 1.2em;
	margin: 0.35em 0;
	max-width: 400px;
	text-align: left;
	width: 100%;
}

/* creates background for text blocks */
.slick-slide p span {
	background-color: #231F20;
	box-shadow: 0 0 0 1px #231F20;
	line-height: 1.1;
	/* fix Firefox */
	box-decoration-break: clone; 
}




/* Dots */
.slick-dots {
	top: 0;
}

.slick-dots li {
	margin: 0;
}

.slick-dots li button:before,
.slick-dots li.slick-active button:before {
	color: #FFF;
	font-size: 14px;
	opacity: 0.6;
}
.slick-dots li.slick-active button:before {
	opacity: 0.9;
}







